.FileInputPanel{display:flex;justify-content:center;height:100%;flex-direction:column;background-color:var(--document-background-color);border-radius:4px}.FileInputPanel .url-input-header{font-size:13px;line-height:16px;padding-inline-start:16px;padding-bottom:8px;font-weight:700}.FileInputPanel .url-input{padding:0 16px;position:relative;display:flex;align-items:center}.FileInputPanel .url-input input:focus{border-color:var(--outline-color)}.FileInputPanel .url-input input:active{border-color:var(--focus-border)}.FileInputPanel .url-input .Icon{position:absolute;inset-inline-end:20px;color:var(--error-border-color)}.FileInputPanel .file-input{padding:6px;width:108px;height:32px}.FileInputPanel .file-input.file-input--error{padding-inline-end:30px;border-color:var(--error-border-color)}.FileInputPanel .url-input-error{padding:0 16px;margin-top:8px;font-size:13px;color:var(--error-text-color)}.FileInputPanel ::-moz-placeholder{color:var(--placeholder-text)}.FileInputPanel ::placeholder{color:var(--placeholder-text)}.FileInputPanel .extension-dropdown{padding:0 16px;display:flex;flex-direction:column}.FileInputPanel .extension-dropdown label,.FileInputPanel .extension-dropdown p{width:100%;font-size:13px;line-height:16px;font-weight:700;order:1;margin-bottom:8px}.FileInputPanel .extension-dropdown label{margin-top:13px}.FileInputPanel .extension-dropdown .Dropdown__wrapper{order:2;width:100%}.FileInputPanel .extension-dropdown .Dropdown__wrapper .Dropdown{width:100%!important}.FileInputPanel .extension-dropdown .Dropdown__wrapper .Dropdown:disabled{border:1px solid var(--gray-4)}.FileInputPanel .extension-dropdown .Dropdown__wrapper .Dropdown:disabled .arrow,.FileInputPanel .extension-dropdown .Dropdown__wrapper .Dropdown:disabled .picked-option{color:var(--gray-5)}.FileInputPanel .extension-dropdown .Dropdown__wrapper .arrow{flex:unset;width:12px;height:16px;margin-top:2px}.FileInputPanel .extension-dropdown .Dropdown__wrapper .Dropdown .picked-option .picked-option-text{flex:none;width:unset}